<template>
  <div class="blogdetail">
    <el-row>
      <el-col :span="2.8"
        ><div class="grid-content bg-purple">
          <div class="blogdetail_left">
            <ul
              class="blogdetail_left_data"
              v-for="(detail_data, index) in leftdata"
              :key="index"
            >
              <li>
                <a href="#">{{ detail_data }}</a>
              </li>
            </ul>
          </div>
        </div></el-col
      >
      <el-col :span="18"
        ><div class="grid-content bg-purple-light">
          <div class="blogdetail_center">
            <div id="advertise"><img src="../assets/images/one.png" alt=""></div>
            <div>动图</div>
            <div>
              <h1>你有新的内容</h1>
              <div class="">多家企业将终止与B栈合作</div>
            </div>
          </div>
        </div></el-col
      >
      <el-col :span="4"
        ><div class="grid-content bg-purple">
          <div class="blogdetail_rightr">
            <div><a href="#">csdn官方博客</a></div>
          </div>
        </div></el-col
      >
    </el-row>

    <!-- deshi -->
  </div>
</template>

<script>
// import images from '../assets/images'
export default {
  data() {
    return {
      leftdata: [
        "推荐",
        "动态",
        "排行榜",
        "程序人生",
        "推荐",
        "动态",
        "排行榜",
        "程序人生",
        "推荐",
        "动态",
        "排行榜",
        "程序人生",
        "",
      ],
    };
  },
};
</script>

<style scoped>
.blogdetail {
  margin: 0px 80px;
}
.el-row {
  margin-bottom: 20px;
}
*/ .bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.blogdetail_left li {
  /* width:px ; */
  height: 30px;
  border: 1px solid grey;
  border-radius: 8px;
  margin: 4px 4px;
  padding: 2px;
  text-align: center;
}
.blogdetail_left li:hover {
  background-color: grey;
}
#advertise{
    margin: 3px;
    height: 120px;
    border: 1px solid red;

}
</style>



